
import React from 'react'
import './Setup.css'
import _ from 'lodash'

class Setup extends React.Component {

  constructor(props) {
    super(props)

    let components = window.localStorage.getItem('components')
    components = components ? JSON.parse(components) : []
    this.state = {
      showLoad: false,
      components: components
    }
  }

  toggleLoad() {
    this.setState({showLoad: !this.state.showLoad})
  }

  updateTitle(title) {
    this.props.update({title: title})
  }

  loadComponent(component) {
    this.props.update(component)
  }

  render() {
    return (
      <div className="Setup">
        <button className={`load
          ${this.state.showLoad ? 'open' : ''}
          ${this.state.components.length ? '' : 'disabled'}
          `} onClick={() => this.toggleLoad()}>
          Load
          <i className="fa fa-chevron-down"></i>

          <ul className="load-options">
            {this.state.components.map((component) => {
              return (
                <li onMouseDown={() => this.loadComponent(component)} className="load-option" key={component.id}>{component.title || 'Untitled Component'}</li>
              )
            })}
          </ul>
        </button>

        <label className="name-label" htmlFor="c_title">Title</label>
        <input className="name-input" type="text" id="c_title" value={this.props.component.title} onChange={(e) => this.updateTitle(e.target.value)} />
      </div>
    )
  }

}

export default Setup
