
import React from 'react'
import './SwatchBuilder.css'
import _ from 'lodash'
import util from '../../services/util'
import SwatchEditor from '../SwatchEditor'

class SwatchBuilder extends React.Component {
  constructor(props) {
    super(props)

    this.state = {}
  }

  update(swatches) {
    this.props.update({swatches: swatches})
  }

  addSwatch() {
    let element = _.find(this.props.component.elements, {selected: true})

    if (!element) {
      return
    }

    let swatches = _.cloneDeep(this.props.component.swatches)

    swatches.push({
      id: util.hashString(),
      title: 'Untitled Swatch',
      element_id: element.id,
      context_id: null
    })

    this.update(swatches)
  }

  renderContainer(element, swatches) {
    swatches = swatches || []

    return (
      <div className="SwatchBuilder">
        {swatches.map((swatch) => {
          return (
            <SwatchEditor update={(component) => this.props.update(component)} key={swatch.id} component={this.props.component} swatch={swatch}></SwatchEditor>
          )
        })}

        <button className={`add-swatch ${element ? '' : 'disabled'}`} onClick={() => this.addSwatch()}>
          add swatch
        </button>
      </div>
    )
  }

  render() {
    let element = _.find(this.props.component.elements, {selected: true})

    if (!element) {
      return this.renderContainer()
    }

    let swatches = _.filter(this.props.component.swatches, {element_id: element.id})

    if (this.props.filterContext) {
      swatches = _.filter(swatches, {context_id: _.find(this.props.component.contexts, {selected: true})})
    }

    return this.renderContainer(element, swatches)
  }
}

export default SwatchBuilder
