
import React from 'react'
import './NavTab.css'
import _ from 'lodash'
import Builder from '../Builder'
import ElementSelector from '../ElementSelector'
import Canvas from '../Canvas'
import SwatchBuilder from '../SwatchBuilder'
import templates from '../../services/templates'
import Save from '../Save'
import util from '../../services/util'
import Setup from '../Setup'

class NavTab extends React.Component {

  constructor(props) {
    super(props)

    this.templates = templates

    this.state = {
      templatesOpen: false,
      component: {
        id: util.hashString(),
        title: '',
        contexts: [],
        elementContexts: [],
        swatches: [],
        styles: [],
        elements: []
      },
      tab: 'setup',
      tabs: [
        {
          id: 'setup',
          title: 'Setup',
          tag: 'Build'
        },
        {
          id: 'elements',
          title: 'Elements',
          tag: 'Build'
        },
        {
          id: 'master',
          title: 'Master',
          tag: 'Design'
        },
        {
          id: 'context',
          title: 'Context',
          tag: 'Design'
        }
      ]
    }
  }

  updateTitle(title) {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)

      component.title = title

      return {
        component: component
      }
    })
  }

  selectTemplate(template) {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)

      component.elements = template.elements
      component.styles = []
      component.swatches = []

      return {
        component: component
      }
    })
  }

  toggleTemplateList(bool) {
    this.setState((prevState) => {
      return {
        templatesOpen: bool !== undefined ? bool : !prevState.templatesOpen
      }
    })
  }

  render_setup() {
    return (
      <div className="setup">
        <Setup update={(component) => this.updateComponent(component)} component={this.state.component}></Setup>
      </div>
    )
  }

  render_elements() {
    return (
      <div className="build">
        <p className="element-cta">Scaffold your component by adding elements, or
          <button className={`element-cta-button ${this.state.templatesOpen ? 'open' : ''}`} onClick={() => this.toggleTemplateList()}>
            choose from a template

            <ul className="template-options">
              {this.templates.map((template) => {
                return (
                  <li onMouseDown={() => this.selectTemplate(template)} key={template.title}>{template.title}</li>
                )
              })}
            </ul>
          </button>
        </p>

        <Builder component={this.state.component} update={(component) => this.updateComponent(component)}></Builder>
      </div>
    )
  }

  render_master() {
    return (
      <div className="master">
        <ElementSelector update={(component) => this.updateComponent(component)} component={this.state.component}></ElementSelector>
        <Canvas component={this.state.component}></Canvas>
        <SwatchBuilder update={(component) => this.updateComponent(component)} filterContext={false} component={this.state.component}></SwatchBuilder>
      </div>
    )
  }

  render_context() {
    return (
      <div className="context"></div>
    )
  }

  updateComponent(component) {
    this.setState((prevState) => {
      let prevComponent = prevState.component

      _.merge(prevComponent, component)

      _.forIn(component, (v, k) => {
        prevComponent[k] = v
      })

      return {
        component: prevComponent
      }
    })
  }

  selectTab(tabId) {
    this.setState({tab: tabId})
  }

  render() {
    return (
      <div className="NavTab">
        <div className="tab-header">
          <ul className="tab-header-list">
            {this.state.tabs.map((tab) => {
              return (
                <li
                  key={`t_${tab.id}`}
                  onClick={() => this.selectTab(tab.id)}
                  className={`tab-header-item ${tab.id === this.state.tab ? 'active' : ''}`}>
                  <small className={`tab-header-sub-title ${tab.tag}`}>{tab.tag}</small>
                  {tab.id === 'setup' && this.state.component.title ? this.state.component.title : tab.title}
                </li>
              )
            })}
          </ul>
          <Save component={this.state.component}></Save>
        </div>

        <div className="tab-body">
          {this[`render_${this.state.tab}`]()}
        </div>
      </div>
    )
  }

}

export default NavTab
