
import React from 'react'
import './Mock.css'
import _ from 'lodash'

class Mock extends React.Component {

  constructor(props) {
    super(props)

    this.state = {}
  }

  renderStyles(elementId) {
    let baseSwatches = _.filter(this.props.component.swatches, {element_id: elementId, context_id: null})
    let styles = {}

    baseSwatches.map((swatch) => {
      let elementStyles = _.filter(this.props.component.styles, {swatch_id: swatch.id})

      elementStyles.map((style) => {
        styles[style.domKey] = style.domValue
      })
    })

    return styles
  }

  renderElement(elements, element) {
    element = element || elements[0]
    let children = _.filter(elements, {parent: element.id})

    let childrenElems

    if (children.length) {
      childrenElems = _.map(children, (childElem) => {
        return this.renderElement(elements, childElem)
      })
    }

    let styles = this.renderStyles(element.id)

    return (
      <div style={styles} title={element.title} key={element.id} className="Element">
        {styles.textContent || null}
        {childrenElems}
      </div>
    )
  }

  render() {
    let elements = this.props.component.elements

    return (
      <div className="Mock">
        {this.renderElement(elements)}
      </div>
    )
  }

}

export default Mock
